
.page {
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #fff;
}
.page > .main {
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
}
.page > .main {
    min-width: 1024px;
    max-width: 1440px;
    margin: 24px auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding: 8 16px;
    width: 100%;
}
#Topbar {
  background: gold;
  -webkit-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25);
          box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25);
  height: 64px;
}
#Editor {
  min-width: 35%;
  background: #444;
}
#Preview {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  margin-left: 16px;
}
svg.icon {
  height: 1em;
  width: 1em;
  fill: currentColor;
  vertical-align: -0.1em;
  font-size: 16px;
}
::-webkit-scrollbar {
  width: 2px;
  /* for vertical scrollbars */
  height: 8px;
  /* for horizontal scrollbars */
  margin-right: 10px;
}
::-webkit-scrollbar-track {
  background: #fff;
  -webkit-box-shadow: inset 1px 0 3px 0 rgba(0, 0, 0, 0.5);
          box-shadow: inset 1px 0 3px 0 rgba(0, 0, 0, 0.5);
}
::-webkit-scrollbar-thumb {
  background: rgba(32, 57, 80, 0.8);
  border-radius: 2px;
}



